<template>
    <div class="notification-wrapper">
        <div class="notification-text">
            <!--            .      .     . -->
            This material is created solely for educational purposes and is intended for healthcare professionals only. Any resemblance to real patients is coincidental. The treatment strategy is determined by the attending physician.
        </div>

        <AcceptCheckbox
        v-if="actualView.acceptCheckbox"  
        :isAccept=isAccept
        @changeAccept = changeAccept
        />

    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { mapActions } from 'vuex'
import AcceptCheckbox from '@/components/game/AcceptCheckbox.vue'

export default {
    name: 'InfoNotification',
    data() {
      return {
        isAccept: false
      }
    },
    components: {
        AcceptCheckbox
    },
    computed: {
      ...mapGetters([
          "actualView"
      ]),
    },
    methods: {
      ...mapActions([
          "setAcceptCheckbox"
      ]),
      changeAccept() {
        this.isAccept = !this.isAccept
        this.setAcceptCheckbox(this.isAccept)
      }
    }
}
</script>

