<template>
    <div class="answer-hint">
        <!--  {{ totalHints }}     .  {{remainedTry}}   {{ totalHints }}. -->
        After three incorrect answers, you will get a hint. You have {{ remainedTry }} attempts out of 3.
    </div>
        <!--       .  {{remainedTry}}   3-. -->
        
</template>

<script>
export default {
    name: 'AnswerHint',
    props: {
      numberTry: Number,
      totalHints: Number,
      hintText: String
    },
    computed:{
        remainedTry(){
            // return this.totalHints - this.numberTry
            return 3 - this.numberTry
        }        
    }
}
</script>

